#container header {
  display: flex;
  justify-content: space-between;

  margin-bottom: 2em;
}

@media (max-width: 500px) {
  #container header {
    flex-direction: column;
    gap: 0.75em;
  }
}

#container h1 {
  line-height: 0.85em;
}

#container h2 {
  font-family: 'Work Sans', sans-serif;
  margin-bottom: 0.5em;
  font-weight: 400;
}

#container section ~ section {
  margin-top: 2.25em;
}

.profileRow {
  display: flex;
  flex-direction: column;
}

.profileRow ~ .profileRow {
  margin-top: 1.75em;
}

#container address {
  font-style: normal;
}

#profileContent {
  display: flex;
  justify-content: space-between;
  gap: 2em;
}

@media (max-width: 500px) {
  #profileContent {
    flex-direction: column;
  }
}

.buttonsContainer {
  display: flex;
  flex-direction: column;

  gap: 1em;
}
